﻿.overlay {
    position: absolute;
    z-index: 9999;
    left: 0px;
    top : 0px;
    width: 100%;
    height: 100%;

    & > .overlay-mask {
        transition: opacity ease-in-out 0.3s;
        background: rgba(0, 0, 0, 0.6);

        position: absolute;
        left: 0px;
        top: 0px;
        width : 100%;
        height: 100%;
    }


    &.no-mask > .overlay-mask {
        background: rgba(0, 0, 0, 0.2);
    }

    & > .overlay-content {
        transform: translateX(0%) translateZ(0px);
        transition: transform ease-in-out 0.3s;
    }

    /*&.overlay-E > .overlay-content {
        margin-left: 44px;
    }

    &.overlay-W > .overlay-content {
        margin-right: 44px;
    }*/

    &:not(.stat-open) {
        display: none;

        & > .overlay-mask {
            opacity: 0;
        }

        &.overlay-E > .overlay-content {
            transform: translateX(100%) translateZ(0px);
        }

        &.overlay-W > .overlay-content {
            transform: translateX(-100%) translateZ(0px);
        }
    }
}


.dialog-content {
    background: #fff;
    position: absolute;
    border-radius: 6px;
    margin: 5px;
    right : 0px;
    top   : 0px;
    transition: opacity ease-in-out 0.2s;
    box-shadow: rgba(0, 0, 0, 0.15) 2px 2px 8px;


    .overlay:not(.stat-open) > & {
        opacity: 0;
    }


    &:after {
        content: "";
        position: absolute;
        top: 100%;
        right: 10px;

        border-width: 6px 8px;
        border-style: solid;
        border-color: transparent;
        border-top-color: #fff;
    }

